/** Media Queries **/
/** UI **/
* {
  margin: 0;
}

header {
  background-color: rgb(230, 227, 221);
  width: 100%;
  height: auto;
  display: flex;
  padding: 5px;
}

.container-logo {
  width: 40%;
  display: flex;
  align-items: center;
}

.logo {
  text-decoration: none;
  color: black;
  display: flex;
  flex-direction: column;
  align-items: center;
  padding-left: 5px;
}

.logo .nombre {
  font-family: "Montserrat", system-ui;
  font-optical-sizing: auto;
  font-size: 1.6rem;
  font-style: normal;
}

.logo .slogan {
  font-family: "Montserrat", system-ui;
  font-optical-sizing: auto;
  font-size: 0.7rem;
  font-style: normal;
}

.container-menu {
  display: flex;
  flex-direction: row;
  flex-wrap: nowrap;
  justify-content: space-around;
  width: 100%;
  align-items: center;
}

ul {
  display: flex;
  flex-direction: row;
  flex-wrap: nowrap;
  justify-content: space-around;
  align-items: flex-end;
}

ul li {
  list-style: none;
  margin: 0 10px;
}

ul li a {
  color: black;
  text-decoration: none;
}

ul li a p:hover {
  text-decoration: white underline 2px;
  transition: ease-out 0.2s;
  color: white;
  font-size: 1rem;
  font-weight: semi-bold;
}

ul li a p {
  font-family: "Montserrat", system-ui;
  font-optical-sizing: auto;
  font-size: 0.9rem;
  font-style: normal;
}